import React from 'react';
import AddPhotoForm from './AddPhotoForm';

class Collection extends React.Component {

handleChange = (e,key) => {
  const photo = this.props.photos[key];
  //take a copy of that photo and update it with the new data
  const updatedPhoto = {
    ...photo,
    [e.target.name]: e.target.value
  }
  this.props.updatePhoto(key, updatedPhoto);
};

renderLogin(){
  return (
    <nav className="login">
    <h2> Inventory </h2>
    <p> Sign in to manage your event collection </p>
    <button className="github" onClick={() =>
       this.props.authenticate('github')}> Log In with Github </button>
       <button className="facebook" onClick={() =>
          this.props.authenticate('facebook')}> Log In with Facebook </button>
      <button className="google" onClick={() =>
          this.props.authenticate('google')}> Log In with Google </button>
    </nav>
  )
}

  renderCollection = (key) => {
    const photo = this.props.photos[key];
    return(
      <div className="photo-edit" key={key}>
        <input type="text" name="name" value={photo.name} placeholder="Photo Name"
          onChange={(e) => this.handleChange(e, key)}/>
        <input type="text" name="price" value={photo.price} placeholder="Photo Price"
          onChange={(e) => this.handleChange(e, key)}/>
        <input type="text" name="src" value={photo.src} placeholder="Photo Source"
        onChange={(e) => this.handleChange(e, key)}/>
        <button onClick={() => this.props.removePhoto(key)}> Remove Photo </button>
      </div>
    )
  };

  render(){
    const logout = <button onClick={this.props.logout}>Log Out</button>
    //check if they are not logged in at all
    if(!this.props.uid){
      return <div>{this.renderLogin()}</div>
    }
    //check if they are the owner of the current event
    if(this.props.uid !== this.props.owner){
      return(
        <div>
        <p> Sorry you are not the owner of this collection! </p>
        {logout}
        </div>
      )
    }

    return(
      <div>
        <h2>Collection</h2>
	{logout}
        {Object.keys(this.props.photos).map(this.renderCollection)}
        <AddPhotoForm addPhoto={this.props.addPhoto}/>
        <button onClick={this.props.loadSamples}>Load Sample</button>
      </div>
    )
  }
  static propTypes = {
    photos: React.PropTypes.object.isRequired,
    loadSamples: React.PropTypes.func.isRequired,
    removePhoto: React.PropTypes.func.isRequired,
    updatePhoto: React.PropTypes.func.isRequired,
    addPhoto: React.PropTypes.func.isRequired,
    uid: React.PropTypes.string,
    owner: React.PropTypes.string,
    authenticate: React.PropTypes.func.isRequired,
    logout: React.PropTypes.func.isRequired,
  };
}

export default Collection;



