import React from 'react';
import { formatPrice } from '../helpers';

class Photo extends React.Component {

  render(){
    const { details, index, order } = this.props;
    if (this.props.display === -1) {
    const buttonText = !order ? 'Add To Order': 'Added';
    const buttonText2 = "It's me!"
    let userButton = null;
    if (this.props.details.tags && this.props.uid && this.props.details.tags.indexOf(this.props.uid) === -1) {
      userButton = <button onClick={() => this.props.addMe(index)} >{buttonText2}</button>
    }
    if (this.props.details.tags && this.props.uid && this.props.details.tags.indexOf(this.props.uid) !== -1) {
      userButton = <button onClick={() => this.props.removeMe(index)} >Not me!</button>
    }
    return(
    <li className="menu-photo">
    <img src={details.src} alt={details.name} />
    <h3 className="photo-name">
      {details.name}
      <span className="price">{formatPrice(details.price)}</span>
    </h3>
    <button disabled={order} onClick={() => this.props.addToOrder(index)} >{buttonText}</button><br/>
    {userButton}


    </li>
  )}
  else return null
}

 static propTypes = {
    details: React.PropTypes.object.isRequired,
    index: React.PropTypes.string.isRequired,
    addToOrder: React.PropTypes.func.isRequired,
    order: React.PropTypes.number,
    display: React.PropTypes.number.isRequired,
    uid: React.PropTypes.string,
    addMe: React.PropTypes.func.isRequired,
    removeMe: React.PropTypes.func.isRequired,
  };
}



export default Photo;



