import React from 'react';

class AddPhotoForm extends React.Component {
  createPhoto(event){
    event.preventDefault();
    const photo = {
      name: this.name.value,
      price: this.price.value,
      src: this.src.value,
      tags: this.tags.value.split(";"),
      display: true
    }
    this.props.addPhoto(photo);
    this.photoForm.reset();
  }
  render(){
    return(
      <form ref={(input) => this.photoForm = input} className="photo-edit" onSubmit={(e) => this.createPhoto(e)}>
      <input ref={(input) => this.name = input} type="text" placeholder="Photo Name"/>
      <input ref={(input) => this.price = input}type="text" placeholder="Photo Price"/>
      <textarea ref={(input) => this.src = input} placeholder="Photo Source"></textarea>
      <input ref={(input) => this.tags = input} type="text" placeholder="Enter tags splitting them by ;"/>
      <button type="submit">+ Add Item</button>
      </form>
    )
  }

  static propTypes = {
    addPhoto: React.PropTypes.func.isRequired
  };
}


export default AddPhotoForm;



