import React from 'react';
import Header from './Header';
import Order from './Order';
import Collection from './Collection';
import samplePhotos from '../sample-photos';

import base from '../base';
import Photo from './Photo';

class App extends React.Component {

  state = {
    photos: {},
    order: {},
    notDisplay: [],
    uid: null,
    owner: null,
    checkLabel: "Find Me!",
  };

  componentDidMount(){
    base.onAuth((user) => {
      if(user){
        this.authHandler(null, { user });
      }
    });
  }

  authenticate = (provider) => {
    base.authWithOAuthPopup(provider, this.authHandler)
  };

  logout = () => {
    base.unauth();
    this.setState({ uid: null});
  };

  authHandler = (err, authData) => {
    if(err) {
      console.error(err);
      return;
    }

    //grab the event info
    const eventRef = base.database().ref(this.props.params.sportsEventId);

    //query the firebase once for the event data
    eventRef.once('value', (snapshot) =>{
      const data = snapshot.val() || {};

      //claim it as our own if there is no owner already
      if(!data.owner) {
        eventRef.set({
          owner: authData.user.uid
        });
      }
      this.setState({
        uid: authData.user.uid,
        owner: data.owner || authData.user.uid
      });
    });
  };

  componentWillMount(){
    //this runs right before the <App> rendered
    this.ref = base.syncState(`${this.props.params.sportsEventId}/photos`
      ,{
        context: this,
        state: 'photos'
      });
    //check if there is any order in localStorage
    const localStorageRef = localStorage
    .getItem(`order-${this.props.params.sportsEventId}`);
    if(localStorageRef) {
      //update our App component's order state
      this.setState({
        order: JSON.parse(localStorageRef)
      });
    }
  }

  componentWillUnMount(){
    base.removeBinding(this.ref);
  }

  componentWillUpdate(nextProps, nextState){
    localStorage.setItem(`order-${this.props.params.sportsEventId}`,
    JSON.stringify(nextState.order));
  }

  addPhoto = (photo) => {
    //update our state
    const photos = {...this.state.photos}; //makes a copy (spread)
    //add our new photo
    const timestamp = Date.now();
    photos[`photo-${timestamp}`] = photo;
    //set state
    this.setState({ photos }) //same as { photos: photos}
  };

  updatePhoto = (key, updatedPhoto) => {
    const photos = {...this.state.photos};
    photos[key] = updatedPhoto;
    this.setState({ photos });
  };

  removePhoto = (key) => {
    const photos = {...this.state.photos};
    photos[key] = null; //we are limited by firebase
    this.setState({ photos });
  };

  loadSamples = () =>{
    this.setState({
      photos: samplePhotos
    })
  };

  addToOrder = (key) => {
    // take a copy of our state
    const order = {...this.state.order};
    //make changes
    order[key] = 1;
    //update our state
    this.setState({ order });
  };

  removeFromOrder = (key) => {
    const order = {...this.state.order};
    delete order[key];
    this.setState({ order });
  };

  findMe = (event) => {
    if (event.target.checked) {
      this.filterPhotos(event, this.state.uid);
      this.setState({checkLabel: "Show Others"});
    }
    else {
      this.filterPhotos(event, null);
      this.setState({checkLabel: "Find me!"});
    }
  };

  foundInTags = (re, photo) => {
    let found = false;
    photo.tags.forEach(tag => {if (re.test(tag)) {
      found = true;
    }});
    return found;
  };

  filterPhotos = (event, me) => {
    const notDisplay = [];
    const regex = me ? new RegExp(me, 'gi') : new RegExp(this.filterTerm.value, 'gi');

    const photos = {...this.state.photos};
    Object.keys(photos).forEach(key => {
      if (!(regex.test(photos[key].name) || this.foundInTags(regex, photos[key]) ||
          (this.filterTerm.value === "" && !me))) {
        notDisplay.push(key);
      }
    });
    this.setState({ notDisplay });
    this.filterForm.reset();
  };


  addMe = (key) => {
    const photos = {...this.state.photos};
    if (this.state.uid) {
      if (photos[key].tags) {photos[key].tags.push(this.state.uid);}
    }
    this.setState({ photos });
  };

  removeMe = (key) => {
    const photos = {...this.state.photos};
    if (this.state.uid && photos[key].tags.indexOf(this.state.uid) !== -1) {
      photos[key].tags.splice(photos[key].tags.indexOf(this.state.uid));
    }
    this.setState({ photos });
  };

  render(){
    let meSearch = null;
    if (this.state.uid) {
      meSearch = <div><input type="checkbox" id="find" ref="find"
      onChange={(e) => this.findMe(e)}
      />
      <label htmlFor="find"> {this.state.checkLabel}</label></div>
    }

    return(
      <div className="photos-sports-events">
      <div className="menu">
        <Header tagline="Photos - Sports Events"/>
        <ul className="list-of-photos">
        <li className="menu-photo">
          {meSearch}
          <form ref={(input) => this.filterForm = input} className="photo-edit" onSubmit={(e) => {this.filterPhotos(e, null); e.preventDefault();}}>
          <input ref={(input) => this.filterTerm = input} type="text" placeholder="Filter"/>
          <button type="submit">Filter </button>
          </form>


        </li>
        {
          Object
          .keys(this.state.photos)
          .map(key =>
            <Photo
              key={key}
              index={key}
              details={this.state.photos[key]}
              addToOrder={this.addToOrder}
              order={this.state.order[key]}
              addMe={this.addMe}
              removeMe={this.removeMe}
              uid={this.state.uid}
              display={this.state.notDisplay.indexOf(key)}
            />)
        }

        </ul>
      </div>
        <Order
          photos={this.state.photos}
          order={this.state.order}
          params={this.props.params}
          removeFromOrder={this.removeFromOrder}
        />
        <Collection
          addPhoto={this.addPhoto}
          photos={this.state.photos}
          loadSamples={this.loadSamples}
          updatePhoto={this.updatePhoto}
          removePhoto={this.removePhoto}
          authenticate={this.authenticate}
          uid={this.state.uid}
          owner={this.state.owner}
          logout={this.logout}
        />
      </div>
    )
  }
  static propTypes = {
    params: React.PropTypes.object.isRequired
  };
}


export default App;



