import React from 'react';
import { formatPrice } from '../helpers';
import CSSTransitionGroup from 'react-addons-css-transition-group';

class Order extends React.Component {

  renderOrder = (key) => {
    const photo = this.props.photos[key];
    const removeButton = <button onClick={() => this.props.removeFromOrder(key)}>Remove</button>

    if(!photo){
      return (<li key={key}>
        Sorry, {photo ? photo.name : 'photo'} is no longer available
        {removeButton}
      </li>)
    }
    return (<li key={key}>
      <span>
      {photo.name}{removeButton}
      </span>
      <span className="price">{formatPrice(photo.price)}</span>
    </li>)
  };

  render(){
    const orderIds = Object.keys(this.props.order);
    const total = orderIds.reduce((prevTotal, key) => {
      const photo = this.props.photos[key];

      if (photo) {
        return prevTotal + (photo.price || 0)
      }
      return prevTotal;
    }, 0); //0 - starting value
    return(
      <div className="order-wrap">
      <h2>Your Order </h2>
      <CSSTransitionGroup
        className="order"
        component="ul"
        transitionName="order"
        transitionEnterTimeout={500}
        transitionLeaveTimeout={500}
      >
        {orderIds.map(this.renderOrder)}
        <li className="total">
          <strong>Total:</strong>
          <span>{formatPrice(total)}</span>

        </li>
      </CSSTransitionGroup>

      </div>
    )
  }
  static propTypes = {
    photos: React.PropTypes.object.isRequired,
    order: React.PropTypes.object.isRequired,
    removeFromOrder: React.PropTypes.func.isRequired,
  };
}



export default Order;



